<script>
import {serverIp} from "../../public/config";

export default {
  name: "User",
  data() {
    return {
      tableData: [],
      multipleSelection: [],
      total: 0,
      pageNum: 1,
      pageSize: 5,
      username: "",
      email: "",
      address: "",
      dialogFormVisible: false,
      form: {},
      options:[],
      uploadUrl:"http://"+serverIp+":9090/user/import"
    }
  },
  created() {
    this.load()
  },
  methods: {
    load() {
      // fetch("http://localhost:9090/user/page?pageNum="+this.pageNum+"&pageSize="+this.pageSize+"&username="+this.username)
      //     .then(res=>res.json())
      //     .then(res=> {
      //           console.log(res)
      //           this.tableData=res.data
      //           this.total=res.total
      //         }
      //     )
      this.request.get("/user/page", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          username: this.username,
          email: this.email,
          address: this.address,
        }
      })
          .then(res => {
            this.tableData = res.records
            this.total = res.total
          })

      this.request.get("/role").then(res=>{
        if(res.code==='200'){
          this.options=res.data
        }
      })
    },
    handleAdd() {
      this.dialogFormVisible = true
      this.form = {}
    },
    handleEdit(row) {
      this.dialogFormVisible = true
      this.form = JSON.parse(JSON.stringify(row))
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
      this.pageSize = val
      this.load()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      this.pageNum = val
      this.load()
    },

    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    reset() {
      this.username = ""
      this.email = ""
      this.address = ""
      this.load()
    },
    save() {
      this.request.post("/user", this.form)
          .then(res => {
            if (res) {
              this.$message.success("保存成功！")
              this.dialogFormVisible = false
              this.load()
            } else {
              this.$message.error("保存失败！")
            }
          })
    },

    del(id) {
      this.request.delete("/user/" + id).then(res => {
        if (res) {
          this.$message.success("删除成功！")
          this.load()
        } else {
          this.$message.error("删除失败！")
        }
      })
    },

    delBatch() {
      if (this.multipleSelection.length > 0) {
        let ids = this.multipleSelection.map(v => v.id)
        this.request.post("/user/del/batch", ids).then(
            res => {
              if (res) {
                this.$message.success("批量删除成功！")
                this.load()
                this.multipleSelection = []
              } else {
                this.$message.error("批量删除失败！")
              }
            }
        )
      }

    },
    exportFile() {
      window.open("http://"+serverIp+":9090/user/export")
    },
    importSuccess() {
      this.$message.success("导入成功！")
      this.load()
    },
    importFail() {
      this.$message.error("导入失败！")
    }
  }
}
</script>

<template>
  <div>


    <div style="margin: 10px 0">
      <el-input style="width:200px" placeholder="请输入用户名" suffix-icon="el-icon-search" class="ml-5"
                v-model="username"></el-input>
      <el-input style="width:200px" placeholder="请输入邮箱" suffix-icon="el-icon-message" class="ml-5"
                v-model="email"></el-input>
      <el-input style="width:200px" placeholder="请输入地址" suffix-icon="el-icon-position" class="ml-5"
                v-model="address"></el-input>
      <el-button class="ml-5" type="primary" @click="load">搜索</el-button>
      <el-button class="ml-5" type="warning" @click="reset">重置</el-button>
    </div>

    <div style="margin: 10px 0">
      <el-button class="ml-5" type="primary" @click="handleAdd">新增 <i class="el-icon-circle-plus-outline"></i>
      </el-button>
      <el-popconfirm
          class="ml-5"
          confirm-button-text='好的'
          cancel-button-text='不用了'
          icon="el-icon-info"
          icon-color="red"
          @confirm="delBatch"
          title="确定批量删除吗？">
        <el-button class="ml-5" type="danger" slot="reference">批量删除 <i class="el-icon-delete"></i></el-button>
      </el-popconfirm>
      <el-upload
          :action='uploadUrl' style="display: inline-block" class="ml-5"
          :show-file-list="false"
          accept="xlsx"
          :on-success="importSuccess"
          :on-error="importFail">
        <el-button class="ml-5" type="primary">导入 <i class="el-icon-bottom"></i></el-button>
      </el-upload>
      <el-button class="ml-5" type="primary" @click="exportFile">导出 <i class="el-icon-top"></i></el-button>
    </div>

    <el-table :data="tableData" border stripe :header-cell-style="{background:'#eef1f6',color:'#606266'}"
              @selection-change="handleSelectionChange">
      <el-table-column
          type="selection"
          width="55">
      </el-table-column>
      <el-table-column prop="id" label="ID" width="80"></el-table-column>
      <el-table-column prop="username" label="用户名" width="140"></el-table-column>
      <el-table-column prop="role" label="角色" width="140"></el-table-column>
      <el-table-column prop="nickname" label="姓名" width="120"></el-table-column>
      <el-table-column prop="phone" label="手机号"></el-table-column>
      <el-table-column prop="email" label="邮箱"></el-table-column>
      <el-table-column prop="address" label="地址"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="success" @click="handleEdit(scope.row)">编辑 <i class="el-icon-edit"></i></el-button>
          <el-popconfirm
              class="ml-5"
              confirm-button-text='好的'
              cancel-button-text='不用了'
              icon="el-icon-info"
              icon-color="red"
              @confirm="del(scope.row.id)"
              title="确定删除吗？">
            <el-button type="danger" slot="reference">删除 <i class="el-icon-delete"></i></el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <div style="padding: 10px 0">
      <el-pagination
          :current-page="pageNum"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>

    <el-dialog title="用户信息" :visible.sync="dialogFormVisible" width="30%">
      <el-form label-width="90px" size="medium">
        <el-form-item label="用户名">
          <el-input v-model="form.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="角色">
          <el-select clearable v-model="form.role" placeholder="请选择角色" style="width: 100%">
            <el-option v-for="item in options" :key="item.name" :label="item.name" :value="item.keyName">
<!--              <i :class="item.value"/> {{item.name}}-->
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="昵称">
          <el-input v-model="form.nickname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="form.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="电话">
          <el-input v-model="form.phone" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="地址">
          <el-input v-model="form.address" autocomplete="off"></el-input>
        </el-form-item>


      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<style scoped>
.headerBg {
  background-color: #eee !important;
}
</style>